*{margin:0;padding:0;box-sizing:border-box}html,body{height:100%}.wrapper{height:100%;display:flex;flex-direction:column}.main-controls{padding:0.5rem 0}canvas{display:block;margin-bottom:0.5rem}#buttons{display:flex;flex-direction:row;justify-content:space-between}#buttons button{font-size:1rem;padding:1rem;width:calc(50% - 0.25rem)}.sound-clips{flex:1;overflow:auto}section,article{display:block}.clip{padding-bottom:1rem}audio{width:100%;display:block;margin:1rem auto 0.5rem}.background{position:absolute;display:block;top:0;left:0;z-index:0}#background{position:relative;top:0;bottom:0;right:0;left:0;background-color:firebrick;height:100%;width:100%}#defaultCanvas0{position:absolute}#primary-sq{height:100px;width:100px;background-color:aquamarine;position:absolute;top:200px;right:200px}.bubble{position:absolute;background-color:orange;border-radius:50%;transition:width .19s, height .19s;width:50px;height:50px;border-radius:0 50% 50% 50%;border:3px solid black;transform:rotate(45deg);margin-top:20px}
